import React from 'react';
import ColorfulButton from './Button';
import { InfoRounded } from '@mui/icons-material';
import { TextField } from '@mui/material';

const StartLessonModal = ({ modalShown = true, disableModal = () => {}, startEvent = () => {} }) => {
    return (
        <div style={{
            zIndex: 100,
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(40, 40, 40, .1)',
            display: modalShown ? 'flex' : 'none',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
            <div style={{
                width: 400,
                height: 350,
                borderRadius: 10,
                backgroundColor: 'white',
                boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                padding: 20
            }}>
                <h4>Начать урок:</h4>
                <h5 style={{
                    whiteSpace: 'break-spaces',
                    marginTop: 10,
                    marginBottom: 20
                }}>     Раздел "Урок" - это инструмент, где вы можете отслеживать успеваемость ваших учеников в режиме реального времени, а также записывать прогресс в журнал и отмечать посещаемость учеников. 
                    </h5>
                <TextField
                    placeholder={"Название урока..."}
                    size={'small'}
                    helperText={"Необязательно"}
                />
                <h6 style={{
                    color: '#ccc',
                    marginTop: 20,
                    marginBottom: 20,
                    whiteSpace: 'break-spaces'
                }}>
                <InfoRounded fontSize={'12'} color={"info"}/>    Как только вы начнёте урок, запустится секундомер для того, чтобы отслеживать проведённое время на уроке. Вы точно хотите начать урок?
                </h6>
                <ColorfulButton
                    text={'Начать урок'}
                    type={'success'}
                    onClick={() => {
                        disableModal()
                        startEvent()
                    }}
                    clearMargin={true}
                />
            </div>
        </div>
    )
};

export default StartLessonModal;